/* eslint-disable */
import "normalize.css";
import "./assets/styles/Style.less";

import Vue from "vue";
import App from "./App.vue";

import router from "./router";
import store from "./store";

import { installFormatDirective } from '@/vue-install/moment-format';
import { registerAsyncDataHook } from '@/vue-install/register-vue-class-component-hook';

import "./registerServiceWorker";
import './plugins/axios';

import Notification from '@/components/notification/Notification.vue'

Vue.use(installFormatDirective);
Vue.use(registerAsyncDataHook);
Vue.config.productionTip = false;

Vue.component("Notification", Notification);

const app = new Vue({
    router,
    store,
    render: h => h(App)
});

router.onReady(async () => {
    router.beforeResolve(async (to, from, next) => {
        const matched = router.getMatchedComponents(to);
        const prevMatched = router.getMatchedComponents(from);

        let diffed = false;
        const activated = matched.filter((component, index) => {
            return diffed || (diffed = (prevMatched[index] !== component));
        });

        if (!activated.length) {
            return next();
        }

        try {
            const hooks = activated.map((c) => c.fetch || c.options && c.options.fetch).filter(_ => _);
            await Promise.all(hooks.map(hook => hook({ store, route: to })));
            if (window) {
                window.scrollTo(0, 0);
            }
            next();
        } catch (error) {
            next(error);
        }
    });

    // base global meta
    await store.dispatch(`global/fetchMeta`);

    // google analytics
    await store.dispatch('global/loadGoogleAnalytics', { router });


    // Fetch initial state
    const initMatched = router.getMatchedComponents(router.currentRoute);
    const asyncDataHooks = initMatched.map((c) => c.fetch || c.options && c.options.fetch).filter(_ => _);
    await Promise.all(asyncDataHooks.map(hook => hook({ store, route: router.currentRoute })));

    // Start!
    app.$mount('#app');
});