const typesData = {
    success: 'success',
    info: 'info',
    warning: 'warning',
    error: 'error'
};

export default {
    name: "Notification",
    props: {
        msg: String,
        type: String,
        visible: Boolean
    },
    computed: {
        typeClass() {
            return this.type ? this.$style[typesData[this.type]] : ''
        }
    }
};