/* eslint-disable */
export default {
    name: "Header",
    props: {
        headerTop: Boolean,
        headerHidden: Boolean
    },
    computed: {
        siteName: function () {
            let siteName = this.$store.state.global.hexoConfig.site.title,
                highlightSiteTitle = this.$store.state.global.themeConfig.highlight_site_title;
            return siteName.split("");
        },
        navItems: function () {
            let menu = this.$store.state.global.themeConfig.menu;
            return Object.keys(menu)
                .map(key => ({
                    name: key,
                    path: menu[key],
                }));
        }
    },
    methods: {
        checkHighlight: function(part) {
            let highlightSiteTitle = this.$store.state.global.themeConfig.highlight_site_title;

            if (!highlightSiteTitle.enable) return false;

            if (highlightSiteTitle.highlight_part.indexOf(part) !== -1) {
                return true;
            }

            return false;
        }
    }
}