import { DisqusOptions } from '@/models/commentSystem.class';


export class Theme {
    menu = new ThemeMenu();
    page_404 = new ThemeCustom404();
    avatar = new ThemeAvatar();
    disqus = new DisqusOptions();
    google_analytics = new GoogleAnalytics();
    background = new ThemeBackground();
    highlight_site_title = new ThemeHighlightSiteName()

    constructor(raw) {
        const raw_theme = raw && raw['theme_config'];
        if (raw_theme) {
            this.menu = new ThemeMenu(raw_theme.menu);
            this.page_404 = new ThemeCustom404(raw_theme.page_404);
            this.background = new ThemeBackground(raw_theme.background);
            this.avatar = new ThemeAvatar(raw_theme.avatar);
            this.disqus = new DisqusOptions(raw_theme.disqus);
            this.google_analytics = new GoogleAnalytics(raw_theme.google_analytics);
            this.highlight_site_title = new ThemeHighlightSiteName(raw_theme.highlight_site_title);
        }
    }
}

export class ThemeCustom404 {
    enable = false;
    source_path = '';

    constructor(raw) {
        if (raw) {
            for (const key of Object.keys(this)) {
                if (raw.hasOwnProperty(key)) {
                    Object.assign(this, { [key]: raw[key] });
                }
            }
        }
    }
}

